  padding: 35px 0;
  display: grid;
  grid-template-columns: 1fr 1fr;
}

.footer__link-logo {
  display: inline-block;
  margin: 0 0 62px 0;
}

.footer__logo-image {
  display: block;
}

.footer__nav-list {
  width: 200px;
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.footer__nav-item {
  cursor: pointer;
  font-family: "CormorantInfant-Bold", "Arial", sans-serif;
  font-weight: 700;
  font-size: 16px;
  line-height: 118%;
}
.footer__nav-item:not(:last-child) {
  margin: 0 0 30px 0;
}

.footer__item-link {
  position: relative;
  text-decoration: none;
  text-transform: uppercase;
  color: #ffffff;
}
.footer__item-link:hover::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 2px;
  background: #444444;
  bottom: -5px;
  left: 0;
}

.footer__social-list {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  grid-row: 1/3;
  grid-column: 2/3;
  margin: 0;
}

.footer__rights-reserved {
  color: #ffffff;
  font-family: "CormorantInfant-Bold", "Arial", sans-serif;
  font-weight: 400;
  font-size: 11px;
  line-height: 131%;
  font-family: "Raleway", "Arial", sans-serif;
}

.footer__rights-reserved span {
  display: inline-block;
  margin: 0 0 36px 0;
}

.footer__link-phone {
  cursor: pointer;
  display: inline-block;
  margin: 0 0 34px 0;
  text-decoration: none;
  color: #ffffff;
  font-family: "CormorantInfant-Bold", "Arial", sans-serif;
  font-weight: 500;
  font-size: 22px;
  line-height: 118%;
  font-family: "Raleway", "Arial", sans-serif;
  font-feature-settings: "tnum" on, "lnum" on;
}
.footer__link-phone:hover {
  color: #444444;
}

.social {
  padding: 10px 0 0 0;
  list-style-type: none;
}
.social__item {
  display: flex;
  justify-content: center;
  width: 30px;
  height: 30px;
}
.social__item:not(:last-child) {
  margin: 0 0 20px 0;
}
.social__link {
  text-decoration: none;
  display: inline-block;
}
.social__image-icon {
  display: block;
}